.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html,
body {
  margin: 0;
  padding: 0;
}

.Sampler {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: calc(100vw - 4rem);
  height: calc(100vh - 4rem);
  margin: 2rem;
  background: rgb(208, 239, 257);
  padding: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 3px;
  -webkit-box-shadow: 0 2px 10px 3px rgba(0, 0, 0, 0.2);
          box-shadow: 0 2px 10px 3px rgba(0, 0, 0, 0.2);
}

.ControlPanel {
  border-bottom: 1px solid #ccc;
  padding-bottom: 1rem;
}

.ControlPanel button,
.ControlPanel input {
  border: 1px solid #ccc;
  background: #fff;
  font-size: 1.5rem;
  margin-right: 1rem;
  border-radius: 3px;
}

.ControlPanel button:hover,
.ControlPanel button:focus {
  background: lightyellow;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.trigger {
  width: 50px;
}

button.beat {
  border: 1px solid #ccc;
  display: inline-block;
  width: calc((100% - 50px) / 16);
  height: 4vw;
  border-radius: 3px;
}

button.beat.on {
  background: #ddd;
}

button.beat.off {
  background: #222;
}

button.beat.playing {
  background: yellow;
}
button.beat.on.playing {
  background: blue;
}



